
.profile_page {
  overflow: auto;


  .email {
    text-align: end;
    margin: 15px 5px;
  }

  .description {
    text-align: center;
    margin: 20px 0;

    font-family: Arial, Helvetica, sans-serif;
    letter-spacing: 0.03em;
    color: rgb(58, 58, 58);
    font-size: 0.95em;
  }

  .center {
    margin: 0 auto;
    display: flex;
    justify-content: center;

    padding-bottom: 40px;
  }

  .btn {
    height: 40px;
    background: rgba(81, 111, 163, 1);
    border: none;
    padding: 0 20px;
    border-radius: 20px;
    color: white;
    cursor: pointer;

    font-family: Arial, Helvetica, sans-serif;
    letter-spacing: 0.05em;
    transition: .3s;
    &:hover {
      background: rgba(81, 111, 163, .8);
    }
  }

  .achievements {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 10px auto;
    width: 80%;
    .achievements_item {


      display: flex;
      flex-direction: column;
      align-items: center;


      margin: 20px;
      
      .img_container {
        display: flex;
        align-items: center;
        justify-content: center;

        width: 100px;
        height: 100px;
        background: rgba(255, 255, 255, 0.5);
        transition: background .3s;
        border-radius: 50%;

        box-sizing: border-box;
        border: 2px solid rgb(202, 202, 202);

        img {
          width: 60%;
          height: 60%;
          opacity: 0.7;
          transition: .3s;
          filter: grayscale(1);
        }
      }

      span {
        margin-top: 5px;
        font-family: Arial, Helvetica, sans-serif;
        letter-spacing: 0.03em;
        font-size: .9em;

        background: white;
        padding: 5px 10px;
        border-radius: 10px;

        // color: rgb(52, 90, 156);
        color: rgb(71, 71, 71);
      }
      &:hover {
        .img_container {
          background: white;

          img {
            filter: grayscale(.8);

          }
        }
      }
    }

    .achievements_item.achieved {
      .img_container {
        border-color: rgb(0, 167, 0);
        background: rgba(255, 255, 255, 1);

        img {
          opacity: 1;
          filter: grayscale(0);
        }
      }

      span {
        color: rgb(52, 90, 156);

      }
    }
  }
}


